import uniIcon from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			city: '深圳市',

			inputValue: '',

			showCloseIcon: false,
			addressList: [{
				id: 1,
				bigAddress: '深圳市市民中心',
				detailAddress: '福中三路市民中心B区首层'
			}, {
				id: 2,
				bigAddress: '深圳市中心',
				detailAddress: '福中三路心B区首层'
			}]
		}

	},
	components: {
		uniIcon
	},
	methods: {
		toChooseCity() {
			uni.navigateTo({
				url:'../cityList/cityList'
			})
		},
		input() {
			if (this.inputValue == '') {
				this.showCloseIcon = false
			} else {
				this.showCloseIcon = true
			}
		},
		deleteInput() {
			this.inputValue = '';
			this.showCloseIcon = false
		}
	}
}
